export default {
	props: {
		goods: Array,
		goToDetail: Function,
		addShopCar: Function,
		showMoney: Boolean
	},
	data() {
		return {
			autoplay: false,
			interval: 2000,
			duration: 500,
			isSplice: false,
			test: '',
			newGoods: this.goods
		};
	},
	computed: {
		dealGoods () {
			let _goods = this.goods;
			if(_goods.length == 1) {
				_goods = _goods.concat([{}, {}])
			}
			if(_goods.length == 2) {
				_goods.push([{}])
			}
			return _goods
		},
		swiperHeight () {
			let height = 0;
			if(this.showMoney) {
				height = uni.upx2px(270) + 'px'
			}else {
					height = uni.upx2px(348) + 'px'
			}
			return height;
		}
	},
	methods: {
		
	}
};
